@use "sass:color";

$primary-color: #c4ceff;
$secondary-color: #94a9fa;
$alt-color: color.adjust($primary-color, $lightness: -20%);
$text-color: #1d1d1b;
// $secondary-color: color.adjust($primary-color, $lightness: 65%);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    font-family: system-ui;
    scroll-behavior: smooth;
}

body{
    padding: 0 20px;
}

#root{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
}

.banner{
    width: 96%;
    border-radius: 8px;
}

h1, h2, h3, h4, h5, h6{
    color: $alt-color;
    text-align: center;
}

p{
    font-size: 18px;
}

nav{
    width: 96%;
    height: 100px;
    background-color: $primary-color;
    border: 2px solid $secondary-color;
    margin-top: 20px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;

    img{
        width: 64px;

        &.atauni-logo{
            width: 80px;
        }
    }

    ul{
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 60px;

        li{

            a{
                text-decoration: none;
                color: $text-color;
                font-weight: bold;

                &:hover{
                    text-shadow: 0px 4px 4px $text-color;
                }
            }
        }
    }
}

section {
    width: 96% !important;
    padding: 32px;
    gap: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    margin-top: 20px;
    position: relative;

    h2{
        display: flex;
        justify-content: center;
        align-items: center;
        &::before {
            content: "";
            position: relative;
            display: inline-block;
            left: -20px;
            width: 60px;
            height: 3px;
            background-color: $alt-color;
        }

        &::after {
            content: "";
            position: relative;
            display: inline-block;
            right: -20px;
            width: 60px;
            height: 3px;
            background-color: $alt-color;
        }
    }

    &:nth-child(2n+1){
        background-color: $primary-color;
        h2{
            color: $text-color;
            &::before {
                background-color: $text-color;
            }
    
            &::after {
                background-color: $text-color;
            }
        }
        p{
            color: $text-color;
        }
    }

    &#intro, 
    &#about{
        p{
            line-height: 36px;
            text-align: justify;
            width: 100%;
            a{
                color: $alt-color;
            }
        }

        .titles{
            margin: 32px 0;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            gap: 24px;

            .title-card{
                width: 240px;
                height: 120px;
                border: 1px solid #1d1d1b;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                background-color: #94a9fa;
                color: #1d1d1b;
                font-weight: bold;
                border-radius: 20px;
                transition: transform .25s;
            
                &:hover{
                    transform: translateY(-8px);
                }
            }
        }

        ul{
            display: flex;
            flex-direction: column;
            gap: 16px;
            align-self: flex-start;
            color: $text-color;
            list-style: none;
            font-size: 18px;
            li{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 8px;
                &:hover{
                    svg{
                        transform: translateX(4px);
                        color: $alt-color;
                    }
                }
            }
        }
    }

